body {
  background: #ececec;
  font-family: "Inter";
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  color: #1b1b1b;
  text-align: center;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

a {
  color: #3a76f0;
  font-weight: 600;
  text-decoration: none;
}

b {
  font-weight: 600;
}

#picture {
  border-radius: 20em;
  width: 20em;
  height: 20em;
  flex-grow: 0;
  flex-shrink: 0;
  user-select: none;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12), 0px 0px 2px rgba(0, 0, 0, 0.08);
}

text {
  pointer-events: all;
  cursor: pointer;
}

.help {
  font-size: 0.8em;
  padding: 2rem;
}

.image {
  position: relative;
}

#canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
}

.upload,
.upload .camera {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.upload .camera {
  width: 3em;
  height: 3em;
  opacity: 0;
  transition: opacity 0.15s linear;
  pointer-events: none;
}

.camera.default,
.upload:hover .camera {
  opacity: 0.8;
}

.upload,
.upload input {
  width: 13em;
  height: 13em;
  cursor: pointer;
}

.upload input {
  opacity: 0;
}

.switch,
.download {
  width: 1.5em;
  height: 1.5em;
  position: absolute;
  bottom: 0;
  display: flex;
  left: 50%;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
}

.switch {
  transform: translate(calc(-50% - 10rem));
  background: var(--color-background);
  border: 2px solid var(--color-accent);
}

.switch::before {
  content: "";
  display: block;
  background: var(--color-accent);
  position: absolute;
  border-radius: 100%;
  width: 1em;
  height: 1em;
}

.download {
  transform: translate(calc(-50% + 10rem));
  background: #fff;
  border: 2px solid #3a76f0;
}

.github-corner svg {
  transition: color 0.1s linear;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

.github-corner:hover svg {
  color: black !important;
}

@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }

  20%,
  60% {
    transform: rotate(-25deg);
  }

  40%,
  80% {
    transform: rotate(10deg);
  }
}

@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }

  .github-corner .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}
